html {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

*, *:after, *:before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    padding: 0;
    margin: 0;
}

:root {
    --body-background-color: #f2f2f2;
    --accent-color: #2e7d32;
    --white-font: #ffffff;
    --card-background-color: #ffffff;
    --spec-background-color: #37474f;
    --grey-background-color: #37474f;
    
    --logo-height: 55px;
    --spec-bar-width: 85px;

    --drop-shadow-mid: 1px 1px 2px 0px rgba(0, 0, 0, .2);
    --drop-shadow-high: 2px 2px 14px 2px rgba(0, 0, 0, .4);

    --sidebar-transition: all ease-in .2s;
}

body {
    background-color: var(--body-background-color);
    overflow-x: hidden;
    overflow-y: scroll;
}

nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    overflow: auto;
    z-index: 99;
    background-color: var(--accent-color);
    color: var(--white-font);
    -webkit-box-shadow: var(--drop-shadow-mid);
            box-shadow: var(--drop-shadow-mid);
    -webkit-transition: var(--sidebar-transition);
    transition: var(--sidebar-transition);
}

nav.min {
    right: var(--spec-bar-width);
}

nav>.logo {
    font-size: 40px;
    height: var(--logo-height);
    margin-left: 20px;
    line-height: var(--logo-height);
}

nav>.right>.raid {
    float: right;
    line-height: var(--logo-height);
    margin-left: 15px;
}

nav>.right>.current-spec {
    height: calc(var(--logo-height) - 10px);
    margin: 5px 5px 5px 20px;
    float: right;
    cursor: pointer;
    background-color: var(--grey-background-color);
    border-radius: 50%;
    padding: 5px;
}

div.main {
    margin-top: var(--logo-height);
}

div.main>.banner {
    width: 100%;
    min-height: 400px;
    background-color: var(--grey-background-color);
    background-image: url('https://mmo-sankar.de/wp-content/uploads/2017/11/World_of_Warcraft_Battle_for_Azeroth_Anduin_vs_Sylvanas_Key_Art_Logo.jpg');
    background-size: 100%;
    background-position-x: 50%;
    background-position-y: 0%;
}

.spec-sidebar {
    position: fixed;
    right: calc(var(--spec-bar-width) * -1);
    width: var(--spec-bar-width);
    top: 0;
    bottom: 0;
    background-color: var(--grey-background-color);
    -webkit-box-shadow: none;
            box-shadow: none;
    -webkit-transition: var(--sidebar-transition);
    transition: var(--sidebar-transition);
}

.spec-sidebar.open {
    right: 0px;
    -webkit-box-shadow: var(--drop-shadow-high);
            box-shadow: var(--drop-shadow-high);
}

.spec-sidebar>.spec {
    text-align: center;
    padding: 10px 0;
}

.spec-sidebar>.spec>img {
    height: var(--logo-height);
    width: var(--logo-height);
    cursor: pointer;
}

.spec-sidebar>.spec.active {
    background-color: var(--accent-color);
}

.spec-sidebar>.spec.all>img {
    background-color: var(--accent-color);
    border-radius: 50%;
}

.spec-sidebar>.spec.all.active>img {
    background-color: var(--grey-background-color);
}

main {
    width: 1000px;
    margin: 0 auto;
    -webkit-transform: translateY(-60px);
            transform: translateY(-60px);
}

.home-select-spec {
    margin-top: 20px;
}

.home-select-spec>p {
    margin-bottom: 10px;
}

.boss-links {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 10px;
    margin-top: 20px;
}

.boss-links>.boss-card {
    min-height: 250px;
    background-size: cover;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
    -webkit-box-shadow: var(--drop-shadow-mid);
            box-shadow: var(--drop-shadow-mid);
    -webkit-transition: all ease-out .2s;
    transition: all ease-out .2s;
}

.boss-links>.boss-card:hover {
    -webkit-box-shadow: var(--drop-shadow-high);
            box-shadow: var(--drop-shadow-high);
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    z-index: 2;
}

.boss-links>.boss-card>div {
    position: absolute;
    bottom: 10px;
    right: 15px;
    color: white;
    font-size: 20px;
}

